* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/* STRUCTURE */
#root {
  width: 100%;
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
}

/* HEADER */
header {
  background-color: #346abc;
  width: calc(100% - 26px);
  padding: 13px;
}

#header-logo {
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -webkit-user-drag: none;
  width: 150px;
  aspect-ratio: 1;
}

/* MAIN */
#select-gui {
  margin: 20px auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 250px;
}
#select-gui p {
  text-align: center;
  margin-bottom: 10px;
}
#topic-selector {
  width: 200px;
}

#display-resources {
  max-width: 500px;
  min-width: 270px;
  margin: 10px auto;
}
#display-resources h1 {
  text-align: center;
  margin: 0 0 20px;
}
#display-resources table {
  border-collapse: collapse;
  width: 100%;
}
#display-resources table, #display-resources table thead th, #display-resources table tbody td {
  border: 1px solid black;
}
#display-resources table thead th, #display-resources table tbody td {
  padding: 5px;
}

/* FOOTER */
footer {
  width: calc(100% - 20px);
  padding: 10px;
  background-color: #2f62ad;
}